.nav-align {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 720px) {
  .nav-link {
    display: flex;
    justify-content: center;
  }
  .navbar-brand {
    display: flex;
    justify-content: center;
    margin: auto;
  }
  .navbar {
    max-width: 100%;
  }
}
